import React from 'react';
import { connect } from 'react-redux';
import {Button, Modal, ModalBody, ModalFooter, ModalHeader, Table} from 'reactstrap';
import IntlMessages from 'util/IntlMessages';
import { getFAQCategories, createFaqCategory, updateFaqCategory } from "actions/FAQ";


class FaqCategories extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            modal: false,
            faqCategory: {
                title: ''
            },
            submitText: 'submit'
        }
    }

    componentDidMount(){
        this.props.getFAQCategories();
    }

    toggle(params, index, id){
        switch (params){
            case 'create':
                this.setState({
                    ...this.state,
                    modal: !this.state.modal,
                    faqCategory : {
                        ...this.state.faqCategory,
                        title: '',
                    },
                    submitText: 'submit'
                });
                break;
            case 'edit':
                this.setState({
                    modal: !this.state.modal
                }, () => {
                    this.props.faq.faqCategories.forEach((item) => {
                        console.log(item);
                        if(item.id === id){
                            console.log(item);
                            this.setState({
                                ...this.state,
                                faqCategory: {
                                    ...this.state.faqCategory,
                                    title: item.title,
                                    faq_category_id: item.id
                                },
                                submitText: 'update'
                            }, () => {
                                console.log(this.state.faqCategory);
                            });
                        }
                    });
                });
                break;
            default:
                this.setState({
                    modal: !this.state.modal
                });
        }

    }

    changeValue(e){
        this.setState({
            ...this.state,
            faqCategory: {
                ...this.state.faqCategory,
                [e.target.name]: e.target.value,
            },
            required: false

        }, () => {
            console.log(this.state.faqCategory);
        })
    }

    submit(params) {
        if(params === 'cancel'){
            this.toggle();
        }else if((params === 'submit') && (this.state.submitText === 'submit')){
            if(this.state.faqCategory.title !== ''){
                this.props.createFaqCategory(this.state.faqCategory);

            }else{
                this.setState({
                    ...this.state,
                    required: true
                })
            }
        }else if((params === 'submit') && (this.state.submitText === 'update')){
            if(this.state.faqCategory.title !== '') {
                this.props.updateFaqCategory(this.state.faqCategory);
            }
        }

    }

    render(){
        return (
            <div className={'faqMainContainer'}>
                <Button className="jr-btn btn-primary text-white mb-0" color="primary" onClick={() =>this.toggle('create', null, null)}>
                    <i className="zmdi zmdi-plus" style={{paddingRight: 5}}/>
                    <span>Create Faq Categories</span>
                </Button>
                <div className={'faqContainer'}>
                    <div className="row">
                        <div className="col-12">
                            <div className="jr-card">
                                {/*<div className="jr-card-header d-flex align-items-center">*/}
                                    {/*<h3 className="mb-0"><IntlMessages id="faq.category"/></h3>*/}
                                    {/*<div className="ml-3">*/}
                                {/*<span className="text-white badge badge-success">*/}
                                    {/*<IntlMessages id="faq.aboutLender"/></span>*/}
                                    {/*</div>*/}
                                {/*</div>*/}
                                <div className="table-responsive-material">
                                    <Table>
                                        <thead>
                                        <tr>
                                            <th>S/N</th>
                                            <th>Faq Category</th>
                                            <th className="">Action</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {this.props.faq.faqCategories.map((data, index) => {
                                            return (
                                                <tr key={data.id}>
                                                    <td>{index + 1}</td>
                                                    <td>{data.title}</td>
                                                    <td>
                                                        <div>
                                                            <Button className="jr-btn btn-primary text-white mb-0" color="primary" onClick={() =>this.toggle('edit', index, data.id)}>
                                                                <i className="zmdi zmdi-comment-edit" style={{paddingRight: 5}}/>
                                                                <span>Edit</span>
                                                            </Button>
                                                        </div>
                                                    </td>
                                                </tr>
                                            )
                                        })}

                                        </tbody>
                                    </Table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Modal isOpen={this.state.modal} toggle={() => this.toggle()} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Create Faq</ModalHeader>
                    <ModalBody>
                        {this.props.faq.faqCategoryCreationMessage !== {} ? <div className={'successCustom'}>{this.props.faq.faqCategoryCreationMessage.Message}</div> : ''}
                        {this.state.required ? <div className={'requiredField'}>All fields are required</div> : null}
                        <div className="form-group row">
                            <label htmlFor={'title'}
                                   className="col-xl-2 col-md-3 col-sm-2 control-label" style={{alignSelf: 'center'}}>Faq Category *</label>
                            <div className="col-xl-10 col-md-9 col-sm-8">
                                <input type="text" className="form-control" id="title"
                                       placeholder="Title" name={'title'} onChange={(event) =>this.changeValue(event)}
                                       value={this.state.faqCategory.title}/>
                            </div>
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="link" onClick={() =>this.submit('cancel')}> Cancel</Button>{' '}
                        <Button color="link" onClick={() => this.submit('submit')}>{this.state.submitText}</Button>
                    </ModalFooter>
                </Modal>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
   faq: state.faq
});

export default connect(mapStateToProps, {getFAQCategories, createFaqCategory, updateFaqCategory})(FaqCategories);


